}
.block {
  display: block;
}
.page-navigation,
.footer {
  text-align: center;
  max-width: 500px;
  margin: 0 auto;
  font-size: 0.8rem;
}
.page-navigation {
  margin: 50px auto 0 auto;
  a {
    display: inline-block;
    text-decoration: none;
    border-bottom: none;
  }
  span {
    display: inline-block;
  }
}
.footer {
  margin-top: 50px;
  color: #777;
  a {
    border: none;
    font-weight: bold;
  }
}
section {
  max-width: 500px;
  margin: 0 auto 50px auto;
  ul {
    list-style: none;
    margin: 0;
    padding: 0;
    li {
      margin: 25px 0;
      .title {
        max-width: 100%;
        //overflow: hidden !important;
        //text-overflow: ellipsis !important;
        //white-space: nowrap !important;
        word-wrap: normal !important;
      }
      a {
        text-decoration: none;
        border: 0;
        // color: #000;
        color: #ffffff;
        box-sizing: border-box;
      }
      .post-date {
        float: right;
        margin: 0 0 0 20px;
      }
      span {
        display: inline-block;
        color: #757575;
        letter-spacing: 1px;
        text-transform: uppercase;
      }
      @media (min-width: 992px) {a {
          border-bottom: 2px solid transparent;
          &:hover {
            border-color: #000;
          }
        }
      }
    }
  }
}
@keyframes bounce {0% {
    transform: translate3d( 0, -1000px, 0 );
  }
  60% {
    transform: translate3d( 0, 25px, 0 );
  }
  75% {
    transform: translate3d( 0, -10px, 0 );
  }
  90% {
    transform: translate3d( 0, 5px, 0 );
  }
  100% {
    transform: none;
  }
}
@-webkit-keyframes bounce {0% {
    -webkit-transform: translate3d( 0, -1000px, 0 );
  }
  60% {
    -webkit-transform: translate3d( 0, 25px, 0 );
  }
  75% {
    -webkit-transform: translate3d( 0, -10px, 0 );
  }
  90% {
    -webkit-transform: translate3d( 0, 5px, 0 );
  }
  100% {
    -webkit-transform: none;
  }
}

.slider {
  width: calc(100% - 7em);
  position: inline-block;
  -webkit-appearance: none;
  height: 5px;
  border-radius: 5px;
  // opacity: 0.85;
  // width: 100%;
  // height: 5px;
  // background: white;
  // outline: none;
  // opacity: 0.7;
  // -webkit-transition: .2s;
  // transition: opacity .2s;

  // Range Handle
  &::-webkit-slider-thumb {
    appearance: none;
    width: 15px;
    height: 15px;
    // opacity: 1 !;
    border-radius: 50%;
    background: white;
    cursor: pointer;
    // border: black;
    // outline: solid;
  }
}

.slider-container {
  width: 100%; 
  text-align: center;
}

.slider-value {
  width: 3em; 
  display: inline-block; 
  background: white; 
  border-radius: 3px; 
  color: black; 
  text-align: center;
  margin-left: 5px;

  // padding: 5px 10px;
  // margin-left: 8px;
  position: relative;

  &:after {
    position: absolute;
    top: 8px;
    left: -7px;
    width: 0;
    height: 0;
    border-top: 7px solid transparent;
    border-right: 7px solid white;
    border-bottom: 7px solid transparent;
    content: '';
  }
}

.slider-label {
  width: 1em; 
  display: inline-block;
}

